import { useState, useEffect } from "react";
import { createBrowserHistory } from "history";

const BASE_NAME =
  process.env.NODE_ENV === "production" ? "/norwegian-dictionary" : "";

const history = createBrowserHistory();

export function useLocation() {
  const [location, setLocation] = useState(history.location);
  useEffect(() => history.listen((location) => setLocation(location)));
  return stripBaseName(location.pathname);
}

export function navigateTo(path: string) {
  const url = `${BASE_NAME}${path}`;
  history.push(url);
}

function stripBaseName(url: string): string {
  if (!url.startsWith(BASE_NAME)) {
    throw new Error(`URL not rooted at path ${BASE_NAME}: ${url}`);
  }

  return url.slice(BASE_NAME.length);
}
